body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  font-size: calc(8px + 2vmin);
  color: white;
}
.contains {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 5vh;
  /* border: 1px solid red; */
}
.contains h1 {
  font-size: 5vh;
}
.contains h1 button {
  font-size: 1vh;
  margin-left: 2vh;
}

.inputWrapper {
  /* border: 1px solid red; */
  margin-top: 4vh;
  margin-bottom: 2vh;
  box-shadow: 6px 9px 11px -1px rgba(212,212,212,0.2);
}

.inputWrapper .TodoInput {
  width: 55vh;
  height: 5vh;
  padding-left: 4vh;
}

.todoList {
  /* border: 1px solid red; */
}
.todoList > li {
  /* border: 1px solid red; */
  margin-top: 2vh;
  padding: 1vh;
  width: 56vh;
  box-shadow: 6px 9px 11px -1px rgba(212,212,212,0.2);
}
.todoList > li:nth-child(odd){
  background-color: #6699ff;
}
.todoList > li:nth-child(even) {
  background-color: #ff9966;
}

.todoList > li > .todoContent {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.todoList > li > .todoContent .title {
  margin-left: 3vh;
}

.todoList > li > .todoContent button {
  font-size: 2vh;
  border-radius: 12%;
  background-color: #4caef0;
}

.UserDialog-Wrapper{ 
  position: fixed; 
  top: 0; left: 0;
  bottom: 0; 
  right: 0; 
  display: -webkit-flex; 
  display: flex; 
  -webkit-justify-content: center; 
          justify-content: center; 
  -webkit-align-items: center; 
          align-items: center;
  
}
.UserDialog {
  border: 1px solid;
  border-radius: 1vh;
  min-width: 48vh;
  border: 1px solid #aaa;
  background-color: #2c3e50;

}
.UserDialog .signInOrSignUp > nav:first-child{ 
  padding: 2vh; 
  margin-left: 5vh;
}
.UserDialog .signInOrSignUp > nav:first-child label{ 
  margin-left: 6vh; 
  cursor: pointer;
}
.UserDialog .forgotPassword > h3 {
   padding: 1vh;
}
.UserDialog form>.row{ 
  display: -webkit-flex; 
  display: flex; 
  padding: 0.8vh 0; 
}
.UserDialog form>.row>label:first-child{
   width: 10vh; 
  }
.UserDialog form>.row.actions{ 
  padding-left: 10vh; 
  display: -webkit-flex; 
  display: flex; 
  -webkit-align-items: center; 
          align-items: center;
 
}

.UserDialog form>.row.actions a {
  margin-left: 2vh;
  color: #fff;
}

.UserDialog button{
  font: inherit; 
  padding: 1vh;
}
